.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    padding: 9px 16px;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    background-color: var(--background-nav);
    border: 1px solid transparent;
    user-select: none;
    color: var(--color);
}

.wrapper.disabled {
    pointer-events: none;
    opacity: 0.5;
}

.title,
.icon {
    margin-left: 5px;
}

.icon {
    display: inline-block;
    width: 24px;
    text-align: center;
}

/* Button types */
.rounded {
    border-radius: 999px;
    box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
    border-color: rgba(22, 24, 35, 0.12);
}

.rounded:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgba(22, 24, 35, 0.03);
}

.primary {
    color: var(--color-white-0);
    background-color: var(--app-color-1);
    border-color: var(--app-color-1);
}

.primary:hover {
    border-color: var(--app-color-1);
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #fe2c55;
}

.outline {
    color: var(--app-color-1);
    border-color: currentColor;
}

.outline:hover {
    border-color: currentColor;
    background-color: rgba(254, 44, 85, 0.06);
}

.text:hover {
    text-decoration: underline;
}

/* Button sizes */
.small {
    min-width: 88px;
    padding: 4px 16px;
}

.large {
    padding: 14px 16px;
    min-width: 140px;
}
